.button {
  cursor: pointer;
  outline: none;
  background-color: transparent;
  color: white;
  border: none;
}
.button:hover {
  opacity: 0.8;
}
.button_disabled {
  background-color: gray;
}
.button__nav {
  height: 100%;
  width: 50px;
}


