header {
    max-width: 1024px;
    height: 100px;
    margin: 0 auto;
}
.header-wrapper {
    width: 100%;
    background-color: #2a1727;
}
header nav {
    display: inline-block;
    text-decoration: none;
    float: right;
    text-transform: uppercase;
    height: 100%;
}
.header__modal-window {
    display: inline-block;
    cursor: pointer;
}
.header__modal-window,
header nav a {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.7);
}
.header__modal-window::before,
header nav::before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 100%;
}
.logo {
    font-size: 18px;
    text-transform: uppercase;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.7);
    float: left;
    height: 100%;
}
.logo::before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 100%;
}
.header__modal-window:hover,
.nav__ref:hover,
.logo:hover {
    opacity: 0.5;
}
.main-text {
    font-size: 18px;
    color: rgb(251, 255, 255);
}
.header__modal-window,
.nav__ref {
    margin-left: 50px;
    color: rgba(255, 255, 255, 0.7);
}
.header__modal-close-up {
    width: 14px;
    height: 14px;
    background-image: url('../images/cross.svg');
    background-size: cover;
    position: absolute;
    top: 20px;
    left: 20px;
}
.header__modal-close-up:hover {
    opacity: 0.8;
}
@media (max-width: 1030px) {
    header {
        padding-left: 20px;
        padding-right: 20px;
    }
}
@media (max-width: 970px) {
    .header__modal-window,
    .nav__ref {
        margin-left: 30px;
    }
    .logo,
    .main-text {
        font-size: 16px;
    }
}
@media (max-width: 850px) {
    .header__modal-window,
    .nav__ref {
        margin-left: 20px;
    }
}
@media (max-width: 770px) {
    .logo,
    .main-text {
        font-size: 14px;
    }
}
@media (max-width: 690px) {
    .header__modal-window,
    .nav__ref {
        margin-left: 10px;
    }
}
@media (max-width: 610px) {
    header {
        padding-left: 0;
        padding-right: 0;
    }
    .logo,
    .main-text {
        font-size: 10px;
    }
}
@media (max-width: 450px) {
    .header__modal-window,
    .nav__ref {
        margin-left: 5px;
    }
    .logo,
    .main-text {
        font-size: 8px;
    }
}


